  .header {
    min-height: 81px;
    border-bottom: 1px solid #0003;
  }
  
  .header-container {
    align-items: center;
    display: flex;
  }
  
  @media screen and (min-width: 767.98px) {
    .header-container {
      align-items: none;
    }
  }
  
  .logo {
    padding: 24px 0;
    font-size: 24px;
    font-weight: 700;
    line-height: 1.375;
    display: inline-block;
    color: var(--text-color);
  }
  
  @media screen and (min-width: 767.98px) {
    .logo {
      padding-top: 32px;
      padding-bottom: 30px;
      font-size: 32px;
      line-height: 1.375;
    }
  }
  
  @media screen and (min-width: 1279.98px) {
    .logo {
      padding: 28px 0;
      font-size: 42px;
      line-height: 1.35714;
    }
  }
  
  .navbar {
    display: none;
  }
  
  @media screen and (min-width: 767.98px) {
    .navbar {
      gap: 44px;
      margin-left: 48px;
      display: flex;
    }
  }
  
  @media screen and (min-width: 1279.98px) {
    .navbar {
      gap: 69px;
      margin-left: 102px;
    }
  }
  
  .navbar__item {
    padding-top: 48px;
    padding-bottom: 36px;
  }
  
  @media screen and (min-width: 1279.98px) {
    .navbar__item {
      padding-top: 55px;
      padding-bottom: 33px;
    }
  }
  
  .navbar__link {
    font-weight: 700;
    line-height: 1.375;
    color: var(--text-color);
  }
  
  @media screen and (min-width: 1279.98px) {
    .navbar__link {
      font-size: 18px;
      line-height: 1.38889;
    }
  }
  
  .navbar__link--current {
    border-bottom: 2px solid var(--accent-color);
    /* padding-bottom: 8px;
    padding-left: 6px;
    padding-right: 6px; */
  }
  
  @media screen and (min-width: 1279.98px) {
    .navbar__link--current {
      padding-bottom: 5px;
    }
  }
  
  .navbar__link:hover, .navbar__link:focus {
    color: var(--accent-color);
  }
  
  .search-field {
    height: 31px;
    background-color: #0000;
    margin-left: auto;
    display: flex;
  }
  
  @media screen and (min-width: 767.98px) {
    .search-field {
      border: 1px solid var(--text-color);
      width: 213px;
      border-radius: 20px;
      margin-top: 44px;
      margin-bottom: 31px;
    }
  }
  
  @media screen and (min-width: 1279.98px) {
    .search-field {
      width: 288px;
      margin: 51px auto 31px;
    }
  }
  
  .search-field input {
    letter-spacing: .02em;
    color: var(--text-color);
    background: none;
    border: none;
    outline: none;
    font-family: Poppins;
    font-size: 12px;
    line-height: 1.75;
  }
  
  .search-field input:focus {
    color: var(--text-color);
  }
  
  .search-field input:-webkit-autofill, .search-field input:-webkit-autofill:hover, .search-field input:-webkit-autofill:focus, .search-field input:-webkit-autofill:active {
    transition: background-color 9999s, color 9999s;
  }
  
  .search-field input:autofill, .search-field input:autofill:hover, .search-field input:autofill:focus, .search-field input:autofill:active {
    transition: background-color 9999s, color 9999s;
  }
  
  @media screen and (max-width: 767.98px) {
    .search-field input {
      width: 0;
      padding: 0;
      transition: all .4s;
    }
  }
  
  @media screen and (min-width: 767.98px) {
    .search-field input {
      padding: 5px 0;
      font-size: 14px;
      line-height: 1.71429;
    }
  }
  
  @media screen and (max-width: 767.98px) {
    .search-field:hover {
      border: 1px solid var(--text-color);
      border-radius: 20px;
      margin-top: 1px;
    }
  
    .search-field:hover input {
      width: 125px;
      padding: 8px 0;
    }
  
    .search-field:hover .search-field__btn {
      padding: 6px 16px 5px 12px;
    }
  
    .search-field__btn {
      color: var(--text-color);
      transition: all .4s;
    }
  }
  
  @media screen and (min-width: 767.98px) {
    .search-field__btn {
      padding: 8px 19px 8px 14px;
    }
  }
  
  .search__icon {
    fill: var(--text-color);
  }
  
  .mobile-button {
    margin-left: 14px;
  }
  
  .theme {
    gap: 8px;
  }
  
  @media screen and (max-width: 767.98px) {
    .theme {
      opacity: 0;
      pointer-events: none;
      visibility: hidden;
      display: none;
    }
  
    .theme.mobile {
      z-index: 6;
      opacity: 1;
      pointer-events: all;
      visibility: inherit;
      display: flex;
      position: absolute;
      bottom: 18px;
    }
  }
  
  @media screen and (min-width: 767.98px) {
    .theme {
      width: 98px;
      height: 21px;
      margin-top: 49px;
      margin-bottom: 36px;
      margin-left: 24px;
      display: flex;
    }
  }
  
  @media screen and (min-width: 1279.98px) {
    .theme {
      width: 148px;
      height: 24px;
      gap: 12px;
      margin: 54px 0 35px;
    }
  }
  
  .mobile-button .icon {
    fill: var(--text-color);
  }
  
  @media screen and (min-width: 767.98px) {
    .mobile-button {
      display: none;
    }
  }
  
  .switch {
    width: 40px;
    height: 20px;
    display: inline-block;
    position: relative;
  }

  .switch input {
    display: none;
  }

  .slider {
    cursor: pointer;
    background-color: var(--white-color);
    border: 1px solid var(--accent-color);
    transition: all .4s;
    position: absolute;
    inset: 0;
  }
  
  .slider:before {
    content: "";
    height: 16px;
    width: 16px;
    background-color: var(--accent-color);
    transition: all .4s;
    position: absolute;
    bottom: 1px;
    left: 1px;
  }
  
  input:checked + .slider:before {
    background-color: var(--white-color);
  }
  
  input:checked + .slider {
    background-color: var(--accent-color);
    border: 1px solid var(--white-color);
  }
  
  input:focus + .slider {
    box-shadow: 0 0 1px var(--accent-color);
  }
  
  input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(20px);
  }
  
  .slider.round {
    border-radius: 34px;
  }
  
  .slider.round:before {
    border-radius: 50%;
  }
  
  .theme__dark, .theme__light {
    display: none;
  }
  
  @media screen and (min-width: 1280px) {
    .theme {
      gap: 12px;
    }
  
    .theme__svg {
      display: none;
    }
  
    .theme__dark {
      color: var(--switcher-gray-moon);
      margin: 0;
      font-size: 18px;
      line-height: 1.3;
      display: block;
    }
  
    .theme__light {
      font-size: 18px;
      line-height: 1.3;
      display: block;
    }
  
    .opacityForDark {
      opacity: .4;
    }
  }

  .theme__light {
    color: var(--switcher-accent-day);
  }
  
  .sun {
    fill: var(--switcher-accent-day);
  }
  
  .moon {
    fill: var(--switcher-gray-moon);
  }

  .mobile-menu {
    width: 100%;
    height: 100vh;
    background-color: var(--bg-color);
    z-index: 5;
    transition: transform .25s cubic-bezier(.12, .56, .79, .6);
    position: fixed;
    top: 0;
    left: 0;
    transform: translateY(-150%);
  }
  
  .mobile-menu.open-menu {
    transform: translateY(0);
  }
  
  @media screen and (min-width: 767.98px) {
    .mobile-menu {
      display: none;
    }
  }
  
  .mobile-menu__container {
    margin: 0 auto;
    padding-left: 16px;
    padding-right: 16px;
  }
  
  @media screen and (min-width: 479.98px) {
    .mobile-menu__container {
      width: 480px;
    }
  }

  .mobile-menu__wrapper {
    justify-content: space-between;
    align-items: center;
    margin-bottom: 68px;
    display: flex;
  }
  
  .close-btn svg {
    stroke: var(--text-color);
    fill: var(--text-color);
  }
  